.card{
    position: relative;
    background-color: white;
    width: 600px;
    padding: 20ox;
    max-width: 85vw;
    height: 50vh;
    box-shadow: 0px 18px 53px 0px gray;
    border-radius: 20px;
    background-size: cover;
    background-position: center;
}
.tinderCards__cardContainder{
    display: flex;
    justify-content: center;
    margin-top: 10vh;
}
.swipe{
    position: absolute;
}
.cardContent{
    width: 100px;
    height: 100px;
}
.card h3{
    position: absolute;
    bottom: 0;
    margin:10px;
    color:blanchedalmond;
}